import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Typography from '@material-ui/core/Typography';
import Avatar from '@material-ui/core/Avatar';
import Toolbar from '@material-ui/core/Toolbar';
import CircularProgress from '@material-ui/core/CircularProgress';
import Tooltip from '@material-ui/core/Tooltip';
import HelpIcon from '@material-ui/icons/HelpOutline';

import CardDetails from './CardDetails';

const styles = theme => ({
  paper: {
    padding: theme.spacing(2),
    textAlign: 'center',
    color: theme.palette.text.secondary,
  },
  helpIcon: {
    marginLeft: theme.spacing(1),
  },
  modal: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
});

class Trending extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      openDialog: false,
      dialogCards: [],
      lastTx: 0,
      cards: [],
      cardsMap: {},
    }

    this.getTrending = this.getTrending.bind(this);
    this.handleOpen = this.handleOpen.bind(this);
    this.handleClose = this.handleClose.bind(this);
  }

  handleOpen = (cardKey) => {
    this.setState({openDialog: true, dialogCards: this.state.cardsMap[cardKey]})
  };

  handleClose = () => {
    this.setState({openDialog: false})
  };

  componentDidMount() {
    this.getTrending()
    this.timer = setInterval(()=> this.getTrending(0,20), 15000);
  }

  componentWillUnmount() {
    clearInterval(this.timer)
    this.timer = null;
  }

  getTrending = () => {
    let cardsMap = this.state.cardsMap
    let cardsArr = []

    fetch('https://api.opensea.io/api/v1/events?asset_contract_address=0x0e3a2a1f2146d86a604adc220b4967a898d7fe07&event_type=successful&limit=300')
    .then(res => {
      return res.json()
    }).then(data => {
      let lastTx = this.state.lastTx
      let txIndex = this.state.lastTx

      data.asset_events.map((card) => {
        let asset = card.asset
        if (asset && card.transaction.id > lastTx) {
          if (cardsMap[asset.image_thumbnail_url]) {
            cardsMap[asset.image_thumbnail_url].push({ name: asset.name, thumbnail: asset.image_thumbnail_url, preview: asset.image_preview_url, price: card.total_price/1000000000000000000, time: card.event_timestamp });
          } else {
            cardsMap[asset.image_thumbnail_url] = [{ name: asset.name, thumbnail: asset.image_thumbnail_url, preview: asset.image_preview_url, price: card.total_price/1000000000000000000, time: card.event_timestamp }]
          }
          txIndex = card.transaction.id > txIndex ? card.transaction.id : txIndex
        }
      })
      this.setState({lastTx: txIndex})
    })

    for (let item in cardsMap) {
      let asset = cardsMap[item]
      cardsArr.push({ name: asset[0].name, thumbnail: asset[0].thumbnail, count: asset.length })
    }

    cardsArr.sort((a,b) => {
      return (a.count < b.count) ? 1 : -1
    })

    this.setState({cards: cardsArr, cardsMap: cardsMap})
  }

  render() {
    const { classes } = this.props;

    return (
      <React.Fragment>
        <Grid item xs={12}>
          <Paper className={classes.paper}>
            <Toolbar>
              <Typography variant="h6">
                Trending Now
              </Typography>
              <Tooltip className={classes.helpIcon} placement="right" title="Shows cards bought since this page was loaded. Auto refreshes every 15s">
                <HelpIcon fontSize="small" />
              </Tooltip>
            </Toolbar>
            {this.state.cards.length <= 0 &&
              <CircularProgress />
            }
            {this.state.cards.length > 0 &&
              <Table size={'small'}>
                <TableHead>
                  <TableRow>
                    <TableCell></TableCell>
                    <TableCell>Name</TableCell>
                    <TableCell>Volume</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {this.state.cards.map((card, index) => {
                    return (
                      <Tooltip key={index} className={classes.helpIcon} placement="right" title="Click for details">
                        <TableRow hover onClick={() => this.handleOpen(card.thumbnail)}>
                          <TableCell><Avatar variant="square" alt={card.name} src={card.thumbnail} /></TableCell>
                          <TableCell>{card.name.substr(0, card.name.indexOf("ID #")-1)}</TableCell>
                          <TableCell>{card.count}</TableCell>
                        </TableRow>
                      </Tooltip>
                    );
                  })}
                </TableBody>
              </Table>
            }
          </Paper>
        </Grid>
        <CardDetails open={this.state.openDialog} cards={this.state.dialogCards} handleClose={this.handleClose}/>
      </React.Fragment>
    );
  }
}

Trending.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(Trending);